.circle {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border-style: solid;
    display: flex;
    align-items: center;
    select {
        flex-grow: 0;
        height: 30px;
        text-align: center;
        border: none;
        background-color: whitesmoke;

        &.rotated {
            transform: rotate(45deg);
        }
    }
    /* select {
        transform: rotate(-45deg);
    } */
}